import '../components/styles/Body.css';
import '../components/styles/Container.css';
import '../pages/styles/Sobre.css';
import capa from '../img/capa_checkfy.jpg';

function Sobre() {
    return (
        <div className="body-sobre">
            <div className="container-sobre">
                <img src={capa} alt="Descrio da imagem" className="imagem-sobre" />
                <div className="container-texto-sobre">
                    <h1 className='titulo-sobre'>Sobre a Checkfy</h1>
                    <p className='texto-sobre'>
                        A Checkfy  uma plataforma inovadora projetada para transformar a maneira como as 
                        empresas realizam avaliaes de processos de software, conforme o modelo MPS.BR. Nosso
                        objetivo  modernizar e simplificar o processo de avaliao, que historicamente  realizado
                        atravs de planilhas e documentos dispersos. Com o Checkfy, trazemos uma soluo digital
                        completa, intuitiva e centralizada.
                    </p>
                    <h2 className='subtitulo-sobre'>MPS.BR</h2>
                    <p className='texto-sobre'>
                        O MPS.BR (Melhoria de Processo do Software Brasileiro)  um modelo de referncia que define
                        prticas e mtodos para melhorar a qualidade de processos e produtos de software em
                        organizaes. No entanto, o processo de avaliao tem sido, at hoje, uma tarefa trabalhosa e
                        manual, dependente de diversas ferramentas, como o Excel. O Checkfy surge como uma soluo
                        para eliminar essa complexidade, reunindo tudo em uma plataforma online moderna.
                    </p>
                    <h2 className='subtitulo-sobre'>Nossa Misso</h2>
                    <p className='texto-sobre'>
                        A misso da Checkfy  facilitar e otimizar o trabalho dos avaliadores e das empresas que buscam
                        certificao MPS.BR, oferecendo um sistema eficiente, confivel e prtico. Acreditamos que, ao
                        digitalizar este processo, no apenas reduzimos o tempo necessrio para concluir avaliaes, mas
                        tambm aumentamos a preciso e a organizao dos dados.
                    </p>
                    <h2 className='subtitulo-sobre'>Projeto Acadmico</h2>
                    <p className='texto-sobre'>
                        A Checkfy  fruto do nosso Projeto Final do curso de Engenharia de Software. O sistema foi
                        cuidadosamente projetado para atender s necessidades reais dos avaliadores e organizaes que
                        se comprometem com a melhoria contnua de processos de software. Cada funcionalidade foi
                        desenvolvida com foco na usabilidade, praticidade e escalabilidade, a fim de garantir que o sistema
                        se adapte facilmente a diferentes contextos e organizaes.
                    </p>
                    <h2 className='subtitulo-sobre'>Por que escolher a Checkfy?</h2>
                    <ul className='texto-sobre'>
                        <li>Automao: Reduz o esforo manual de consolidao de dados.</li>
                        <li>Centralizao: Todos os documentos e informaes de avaliao em um s lugar.</li>
                        <li>Acessibilidade: Plataforma web de fcil acesso e navegao.</li>
                        <li>Agilidade: Acelera o processo de avaliao e emisso de relatrios.</li>
                        <li>Preciso: Minimizamos erros humanos ao automatizar grande parte do processo.</li>
                    </ul>
                    <p className='texto-sobre'>
                        Estamos comprometidos em entregar uma ferramenta que faa a diferena no dia a dia dos avaliadores
                        e empresas que buscam a excelncia em seus processos de desenvolvimento de software.
                    </p>
                </div>
            </div>
        </div>
    );
}

export default Sobre;