import React, { useState, useEffect } from 'react';
import { getAvaliacaoById, inserir_ata_reuniao } from '../services/Api';
import { useNavigate } from 'react-router-dom';
import '../components/styles/Body.css';
import '../components/styles/Container.css';
import '../components/styles/Form.css';
import '../components/styles/Button.css';
import '../components/styles/Etapas.css';
import '../components/styles/EtapaAtividadesPlanejamento.css';

function AtaReuniaoAbertura({onNext, avaliacaoId }) {
  const [ataReuniao, setAtaReuniao] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    if (avaliacaoId) {
      carregarAtaReuniao();
    }
  }, [avaliacaoId]);

  const carregarAtaReuniao = async () => {
    try {
      const data = await getAvaliacaoById(avaliacaoId);
  
      if (data) {
        setAtaReuniao(data.ata_reuniao_abertura || '');
      }
    } catch (error) {
      console.error('Erro ao carregar dados da ata de reunio:', error);
    }
  };
  
  const salvarAtaReuniao = async () => {
    try {
      const data = {
        ataReuniao: ataReuniao,
      };

      await inserir_ata_reuniao(avaliacaoId, data);
      alert('Ata de reunio salva com sucesso!');
    } catch (error) {
      console.error('Erro ao salvar a ata de reunio:', error);
      alert('Erro ao salvar a ata de reunio. Tente novamente.');
    }
  };

  return (
    <div className='container-etapa'>
      <div className='title-container'>
        <h1 className='title-form'>ATA DE REUNIO DE ABERTURA DA AVALIAO FINAL</h1>
      </div>
        <div className='dica-div'>
            <strong className='dica-titulo'>Observao:</strong> {/* Texto de dica mais escuro */}
            <p className='dica-texto'>
            1 - Avaliador Lder: Apresentou os nveis MR-MPS e o processo de avaliao. Explicou o Acordo de Confidencialidade, escopo e nvel de maturidade pleiteado.
            </p>
            <p className='dica-texto'>
            2 - Patrocinador: Reforou o motivo da avaliao e a importncia do apoio dos colaboradores. Destacou a prioridade de respeitar horrios e ser sincero nas entrevistas.
            </p>
            <p className='dica-texto'>
            3 - Cronograma: O cronograma da avaliao foi apresentado e todos os participantes foram informados dos momentos de suas contribuies.
            </p>
            <p className='dica-texto'>
            4 - Presena: Todos os colaboradores entrevistados devem estar presentes. Ausentes por fora maior sero informados no incio de suas entrevistas.
            </p>
        </div>
        <label className="label-etapas">Ata de Reunio:</label>
        <div className='dica-div'>
          <strong className='dica-titulo'>Observao:</strong>
          <p className="dica-texto">
            Dica para preencher: adicionar dica
          </p>
        </div>
        <br></br>
        <div className='textarea-wrapper'>
        <textarea
          className="input-textarea-avaliacao"
          value={ataReuniao}
          onChange={(e) => setAtaReuniao(e.target.value)}
          rows="4"
        ></textarea>
      </div>

      <button className='button-save' onClick={salvarAtaReuniao}>SALVAR</button>
      <button className='button-next' onClick={onNext}>PRXIMA ETAPA</button>
    </div>
  );
}

export default AtaReuniaoAbertura;
