import React, { useState } from 'react';
import '../components/styles/Body.css';
import '../components/styles/Container.css';
import '../components/styles/Form.css';
import '../components/styles/Button.css';
import '../components/styles/Etapas.css';
import '../components/styles/EtapaConfirmarAvaliacaoFinal.css';
import { enviarEmailAuditorAvaliacaoFinal } from '../services/Api';

function EtapaConfirmarAvaliacaoFinal({ onNext, avaliacaoId }) {
  const [loading, setLoading] = useState(false);
  const [confirmado, setConfirmado] = useState(false);

  const handleConfirmarAvaliacao = async () => {
    const confirmacao = window.confirm(
      'Tem certeza que deseja confirmar a avaliao final? Isso no poder ser desfeito.'
    );
    
    if (!confirmacao) return;

    setLoading(true);
    try {
      await enviarEmailAuditorAvaliacaoFinal(avaliacaoId);
      setConfirmado(true);
      alert('Auditoria da avaliao final solicitada com sucesso!');
    } catch (error) {
      console.error('Erro ao solicitar a auditoria da avaliao final:', error);
      alert('Erro ao solicitar a auditoria da avaliao final.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className='container-etapa'>
      <h2 className='title-form'>CONFIRMAR AVALIAO FINAL</h2>
      <div className='dica-div'>
        <strong className='dica-titulo'>Observao:</strong>
        <p className='dica-texto'>
          Ao clicar no boto abaixo, voc estar solicitando a auditoria da avaliao final. 
          Tenha certeza de que todos os dados esto corretos antes de continuar.
        </p>
      </div>
      <button
        className='button-solicitar-auditoria'
        onClick={handleConfirmarAvaliacao}
        disabled={loading || confirmado}
        style={{
          backgroundColor: confirmado ? 'gray' : '',
          cursor: confirmado ? 'not-allowed' : 'pointer'
        }}
      >
        {loading ? 'Enviando email...' : confirmado ? 'AUDITORIA SOLICITADA' : 'SOLICITAR AUDITORIA'}
      </button>
      <br />
      <button className='button-next' onClick={onNext}>PRXIMA ETAPA</button>
    </div>
  );
}

export default EtapaConfirmarAvaliacaoFinal;